.wrapper {
  padding: 60px 100px;
}

@media screen and (max-width: 1250px) {
  .wrapper {
    padding: 50px 50px;
  }
}

@media screen and (max-width: 1000px) {
  .wrapper {
    padding: 50px 40px;
  }
}

@media screen and (max-width: 600px) {
  .wrapper {
    padding: 50px 20px;
  }
}

@media screen and (max-width: 450px) {
  .wrapper {
    padding: 50px 10px;
  }
}