// Fonts
$font-dm-sans: "DM Sans";
$outfit: "Outfit";

// Font sizes
$font-size-base: 16px;
$font-size-5xl: 24px;
$font-size-13xl: 32px;
$font-size-mini: 15px;
$font-size-29xl: 48px;

// Colors
$color-white: #fff;
$color-black: #000;
$color-gray-100: rgba(255, 255, 255, 0.5);
$color-darkslategray-100: #404040;
$color-darkslategray-200: rgba(64, 64, 64, 0.5);
$color-cornflowerblue: #3d92ed;
$color-palegreen: #98fb98;
$color-darkturquoise: #00ced1;

// Border radiuses
$br-81xl: 100px;

.mainTop {
  .lottiimage {
    width: 100%;
    position: relative;
  }

  .frame_child,
  .frame_item {
    position: absolute;
    top: 0;
    right: 0;
    border-radius: 50%;
    background-color: $color-palegreen;
    filter: blur(280px);
    width: 30%;
    height: 50%;
  }

  .frame_item {
    bottom: 100%;
    z-index: 3;
    opacity: 0.4;
    left: 80%;
  }

  .frame_child,
  .frame_below {
    position: absolute;
    right: 0;
    bottom: 0;
    border-radius: 40%;
    background-color: $color-cornflowerblue;
    filter: blur(280px);
    width: 20%;
    height: 70%;
  }

  .frame_below {
    z-index: 3;
    top: 50%;
    right: 85%;
    opacity: 0.4;
  }
}

.main {
  .card1 {
    border: none !important;
    box-shadow: none !important;
  }

  $spacing-value: 10px;

  .media {
    z-index: -1;
    background-color: $color-cornflowerblue;
  }

  .typo {
    display: flex;
    flex-direction: column;
    align-items: center;
    position: relative;
    z-index: 3;
    flex-grow: 1;
    margin-bottom: 20px;

    &> :not(:first-child) {
      margin-top: $spacing-value;
    }

    .title {
      color: $color-cornflowerblue;
      font-size: 30px;
      font-style: normal;
      font-weight: 600;
      margin-bottom: 0;

      @media screen and (max-width: 600px) {
        font-size: 28px;
      }
    }

    .subtitle {
      color: $color-darkslategray-100;
      font-family: $outfit;
      text-align: center;
      font-size: 20px;
      font-style: normal;
      font-weight: 400;
      line-height: 1;
      margin-top: 0;

      @media screen and (max-width: 600px) {
        font-size: 18px;
      }
    }

  }
}

.gridItem {
  padding: 20px;

  .maintext {
    color: $color-darkslategray-100;
    font-size: 24px;
    font-weight: 600;
    text-align: left;
    font-family: $outfit;

    @media (max-width: 960px) {
      font-size: 27px;
    }
  }

  @media screen and (max-width: 280px) {
    padding: 0;
  }

  .subtext {
    color: $color-darkslategray-100;
    font-size: 15px;
    text-align: left;
    line-height: 17.75px;
    font-family: $outfit;
  }
}

.freespace {
  padding: 49px;

  @media (max-width: 600px) {
    padding: 25px;
  }
}

.mainGridContainer {
  position: relative;
  margin: 0 0 40px 0;
  width: 100%;
}