.withPadding {
  background: linear-gradient(90deg,
      rgba(63, 212, 238, 0.1) 28.96%,
      rgba(75, 155, 238, 0.1) 74.9%),
    linear-gradient(0deg, #ffffff, #ffffff);
}

.maintop {

  .maintext {
    text-align: center;
    color: #3d92ed;
    font-family: "Outfit";
    font-size: 30px;
    font-style: normal;
    font-weight: 700;
    margin-bottom: 20px;
  }
}

.grid {
  width: auto !important;
  margin: 0 !important;
  display: flex;
  justify-content: center;
  align-items: center;

  .gridsub {
    position: relative;
    padding: 20px;

    .images {
      margin-top: 0;

      .media {
        width: 140px;
        height: 140px;
        object-fit: contain;
        margin-bottom: -45px;
      }
    }
  }

  .maintitle {
    padding: 10px;
    text-align: center;
    color: #3d92ed;
    font-family: "Outfit";
    font-size: 48px;
    font-style: normal;
    font-weight: 700;
    margin-bottom: 20px;

    @media (max-width: 959px) {
      margin-bottom: 100px;
    }
  }

  .card {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: flex-start;
    flex-shrink: 0;
    border-radius: 20px;
    background: linear-gradient(0deg, #fff 0%, #fff 100%),
      linear-gradient(90deg,
        rgba(63, 212, 238, 0.1) 28.96%,
        rgba(75, 155, 238, 0.1) 74.9%);
    box-shadow: 0px 11px 26px 7px rgba(0, 0, 0, 0.08);
    padding: 40px 20px 20px 20px;
  }

  .textcontainer {
    padding: 0;
    padding-bottom: 0;

    .text {
      color: #404040;
      text-align: center;
      font-family: "Outfit";
      font-size: 20px;
      font-style: normal;
      font-weight: 400;
      margin-bottom: 0;
      padding: 0;
    }
  }

  //
  @media (max-width: 2480px) {
    align-items: center;
  }

  @media (max-width: 600px) {
    .maintitle {
      font-size: 24px;
    }

    .card {
      align-items: center;
    }
  }

  @media (max-width: 464px) {
    .maintitle {
      font-size: 20px;
    }
  }
}