@import "../../utils/variables.scss";

.withPadding {
  position: relative;
  min-height: 700px;
  max-height: 90vh;
  padding-top: 60px;
  padding-bottom: 60px;
  flex-shrink: 0;
  background:
    linear-gradient(90deg, rgba(1, 1, 1, 0.3) 28.96%, rgba(0, 0, 0, 0) 51.12%),
    linear-gradient(90deg,
      rgba(1, 1, 1, 0.5) 28.96%,
      rgba(13, 32, 35, 0.14) 74.9%),
    url("./LAND1.jpg") no-repeat center/cover,
    lightgray 50%;
  background-position: top center;

  .graphic {
    position: absolute;
    left: 0;
    top: 400px;
    z-index: -1;

    img {
      width: 250px;
    }
  }

  .wrapper {
    display: flex;
    justify-content: space-between;

    p.error {
      color: red;
      margin-top: -10px;
      margin-bottom: 1em;
    }

    .left {
      padding-top: 250px;
      z-index: 3;

      .header {
        * {
          font-family: "Mulish", sans-serif;
          color: #ffffff;
        }

        line-height: 58.67px;
        font-size: 45px;
        font-weight: 700;

        div {
          margin-bottom: 10px;
          font-weight: 600;
          line-height: 1;

        }
      }

      .header.mobile {
        display: none;

        span {
          color: $themePurple;
        }
      }

      .subheader {
        padding-top: 1em;
        line-height: 23.6px;
        color: #ffffff;

        div {
          font-size: 16px;
        }
      }

      .subheader.mobile {
        display: none;
      }
    }

    .right {
      padding-top: 100px;
      position: relative;
      z-index: 1;

      img {
        max-width: 850px;
        position: absolute;
        top: 150px;
        right: 0;
      }
    }

    .cta {
      margin-top: 23px;

      form {
        .textField {
          margin-bottom: 20px;

          input {
            width: 250px;
            border-radius: 5px;
            height: 40px;
            box-shadow: 0 0 6px 0 rgba(0, 0, 0, 0.1);
            border: none;
            padding-left: 10px;
          }
        }
      }

      button {
        margin-right: 10px;
        width: 300px;
        height: 40px;
        font-size: 18px;
        border-radius: 40px !important;
        z-index: 2;
        background-color: $themePurple;
        color: #ffffff;
        font-weight: 600;
        line-height: 23.6px;
        font-family: "Outfit";
      }

      .button2 {
        -webkit-box-shadow: 0px 0px 25px 18px rgba(63, 212, 238, 0.2);
        -moz-box-shadow: 0px 0px 25px 18px rgba(63, 212, 238, 0.2);
        box-shadow: 0px 0px 25px 18px rgba(63, 212, 238, 0.2);
        margin-right: 10px;
        border-radius: 40px !important;
        z-index: 2;
        background-color: #ffffff;
        color: $themePurple;
        font-family: "Outfit";
      }
    }
  }
}

// Button.module.scss

/* Graphic becomes smaller */
@media screen and (max-width: 1250px) {
  .withPadding {
    .wrapper {
      .left {
        padding-top: 200px;
      }

      .right {
        img {
          width: 600px;
        }
      }
    }
  }
}

/* Graphic becomes smaller still */
@media screen and (max-width: 1150px) {
  .withPadding {
    .wrapper {
      .right {
        img {
          top: 200px !important;
        }
      }
    }
  }
}

/* Graphic becomes smaller and smaller still */
@media screen and (max-width: 900px) {
  .withPadding {
    .wrapper {
      .right {
        img {
          width: 500px !important;
          top: 300px !important;
        }
      }
    }
  }
}

/* Switch to column */
@media screen and (max-width: 650px) {
  .withPadding {
    .graphic {
      top: 70px;

      img {
        width: 110px;
      }
    }

    .wrapper {
      flex-direction: column;

      .left {
        padding-top: 50px;
        order: 2;

        .header {
          line-height: 38.67px;
          font-size: 80px;
          font-weight: 700;
          font-size: 40px;
          color: #ffffff;
          padding-top: 100px;
        }

        .subheader {
          line-height: 21.67px;
          font-weight: 200;
          font-size: 20px;
          color: #ffffff;
          padding-top: auto;
        }

        .cta {
          width: 100%;
          margin-top: 20px;

          form {
            width: 100%;

            input {
              width: 100% !important;
            }

            button {
              width: 100% !important;
            }

            .button2 {
              width: 100% !important;
            }
          }
        }
      }

      .right {
        order: 1;
        display: flex;
        justify-content: center;
        position: static;

        img {
          position: static;
          width: 500px !important;
          top: 300px !important;
        }
      }
    }
  }
}

@media screen and (max-width: 500px) {
  .withPadding {
    .wrapper {
      .header {
        display: none;
      }

      .header.mobile {
        display: block !important;
      }

      .subheader {
        display: none;
      }

      .subheader.mobile {
        display: block !important;
      }

      .right {
        img {
          width: 100% !important;
        }
      }
    }
  }
}

@media screen and (min-width: 280px) and (max-width: 500px) {
  .withPadding .wrapper .cta button {
    width: 270px;
  }

  .withPadding .wrapper .cta button2 {
    width: 270px !important;
  }
}

@media screen and (min-width: 170px) and (max-width: 280px) {
  .withPadding .wrapper .cta button {
    width: 170px;
  }

  .withPadding .wrapper .cta button2 {
    width: 170px;
  }
}

@media screen and (min-width: 50px) and (max-width: 170px) {
  .withPadding .wrapper .cta button {
    width: 50px;
  }

  .withPadding .wrapper .cta button2 {
    width: 50px;
  }
}