$themePurple: #3d92ed;
/* Replace with your actual purple color */

.wrapper {
  background:
    url("./revBack.png") no-repeat center/cover,
    lightgray 50%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  padding-top: 5em;
  padding-bottom: 5em;
  width: 100%;
  height: auto;
  /* Change to auto to allow content to expand */
  flex-shrink: 0;

  @media screen and (max-width: 1100px) {
    padding-top: 3em;
    padding-bottom: 4em;
    padding-left: 2em;

    .review_card {
      margin-bottom: 40px;
    }
  }

  @media screen and (max-width: 600px) {
    padding-top: 2em;

    .review_card {
      margin-bottom: 30px;
      width: 100%; // Make the card full width on smaller screens
    }
  }

  .header {
    text-align: center;
    color: #3fd4ee;
    font-family: "Outfit";
    font-size: 38px;
    font-weight: 700;
    margin-bottom: 5px;
    line-height: 47.2px;

    @media screen and (max-width: 600px) {
      font-size: 30px;
      margin-top: 0px;
      margin-bottom: 10px;
    }
  }

  .sub_header {
    text-align: center;
    font-size: 20px;
    color: #ffff;
    margin-bottom: 100px;

    @media screen and (max-width: 600px) {
      font-size: 18px;
      margin-bottom: 2em;
    }
  }

  .swiperContainer {
    padding-top: 3em !important;
    padding-bottom: 4em !important;
    width: 100%;
    display: flex;
    justify-content: center;
    margin-bottom: 25px;
    align-items: center;

    .swiperslide {
      margin-right: 20px;
      width: 260px;
      height: 365px;
      flex-shrink: 0;
      border-radius: 10px;
      z-index: 2;
      background: linear-gradient(180deg,
          rgba(63, 212, 238, 0.1) 0%,
          rgba(0, 0, 0, 0) 18.23%),
        rgba(255, 255, 255, 0.1);
      backdrop-filter: blur(7px);
      position: relative;
      padding-top: 1em;

      @media screen and (max-width: 1200px) {
        padding-bottom: 20px;
        height: auto;
      }

      @media screen and (max-width: 600px) {
        width: 100%;
      }

      .profile_photo {
        width: 80px;
        height: 80px;
        border-radius: 50%;
        overflow: hidden;
        position: absolute;
        top: -40px;
        left: 50%;
        transform: translateX(-50%);
        border: 3px solid $themePurple;
        z-index: 9;
        background-size: cover;
        background-position: center;
        box-shadow: 0px 29px 22px 4px #00000080; // Add drop shadow

        @media screen and (max-width: 600px) {
          width: 60px;
          height: 60px;
          top: -30px;
        }

        img {
          object-fit: cover;
        }
      }

      .card_content {
        text-align: center;
        margin-top: 40px;

        .name {
          font-family: "Outfit";
          font-size: 20px;
          font-weight: 510;
          line-height: 24px;
          letter-spacing: 0px;
          color: #ffffff;
          margin-bottom: 10px; // Add margin for spacing

          @media screen and (max-width: 600px) {
            font-size: 18px;
          }
        }

        .place {
          font-family: "Outfit";
          font-size: 14px;
          font-weight: 274;
          line-height: 17px;
          letter-spacing: 0px;
          text-align: center;
          color: #ffffff;
          opacity: 0.5;
          margin-bottom: 10px; // Add margin for spacing

          @media screen and (max-width: 600px) {
            font-size: 12px;
          }
        }

        .star {
          width: 152px;
          height: 24px;
          margin: 0 auto; // Center the image
          margin-bottom: 10px; // Add margin for spacing

          @media screen and (max-width: 600px) {
            width: 120px;
            height: 20px;
          }
        }

        .about {
          font-family: "Outfit";
          font-size: 16px;
          font-weight: 274;
          line-height: 24px;
          letter-spacing: 0px;
          text-align: center;
          color: #ffffff;
          padding: 1em;
          margin-bottom: 10px; // Add margin for spacing

          @media screen and (max-width: 600px) {
            font-size: 18px;
            padding: 20px;
          }

          @media screen and (max-width: 600px) {
            font-size: 14px;
            padding: 1px;
          }
        }
      }
    }
  }
}