@import "../../utils/variables.scss";

.wrapper {
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
  background-color: lightgray;
  padding-block: 5em;

  .mobGradient {
    @media screen and (max-width: 959px) {
      background: linear-gradient(180deg, rgba(255, 255, 255, 0.5) 0%, rgba(255, 255, 255, 0.5) 100%);
      padding: 40px;
      border-radius: 40px;

    }
  }


  .heading {
    color: #3d92ed;
    font-size: 26px;
    font-weight: 600;
    margin-bottom: 10px;
  }

  .paragraph {
    color: #404040;
    font-size: 16px;
    margin-bottom: 20px;
    line-height: 1.5;
  }

  .button {
    padding: 9px 60px;
    background-color: #000000;
    border-radius: 40px;
    color: #fff;
    font-weight: 400;
    font-size: 18px;
    min-width: 240px;
  }
}